<template>
    <h1>auth stuff</h1>
    <button v-if="!loggedIn" v-on:click="signIn">Sign in</button>
    <button v-if="loggedIn"  v-on:click="signOut">Sign out</button>
    <button v-if="loggedIn"  v-on:click="editProfile">Edit profile</button>
</template>
  
<script>
    import { isLoggedIn, signIn, signOut, editProfile } from './../authConfig';

    const loggedIn = isLoggedIn();

    export default {
        name: 'LoginComponent',
        data() {
            return {
                loggedIn
            };
        },
        methods: {
            async signIn() {
                signIn();
            },
            async signOut() {
                signOut();
            },
            async editProfile() {
                editProfile();
            }
        }
    }
</script>





