import { createApp } from 'vue'
import App from './App.vue'
import store from './store';
const msal = require('@azure/msal-browser');
var config = require('./authConfig');

const msalInstance = new msal.PublicClientApplication(config.msalConfig);
store.dispatch('setMsalInstance', msalInstance);

msalInstance.initialize().then(() => {
    msalInstance.handleRedirectPromise().then(config.handleResponse).catch(err => {
        console.error(err);
    });
})

var app = createApp(App);
app.use(store);
app.mount('#app')
