import { createStore } from 'vuex';

const store = createStore({
    state: {
        msalInstance: null,
        accountId: ""
    },
    mutations: {
        setMsalInstance(state, msalInstance) {
            state.msalInstance = msalInstance;
        },
        setAccountId(state, accountId) {
            state.accountId = accountId;
        }
    },
    actions: {
        setMsalInstance({ commit }, msalInstance) {
            commit('setMsalInstance', msalInstance);
        },
        setAccountId({ commit }, accountId) {
            commit('setAccountId', accountId);
        }
    },
    getters: {
        getMsalInstance: (state) => state.msalInstance,
        getAccountId: (state) => state.accountId
    }
});

export default store;